@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-style: normal;
  padding: 15px;
  background: #f0dcdc;
}

button {
  padding: 2px 5px;
}

form, table {
  margin: auto;
}

th,
td {
  text-align: left;
  background-color: #fefefe;
  padding: 2px 8px;
}

form {
  max-width: 500px;
  background-color: #fefefe;
  padding: 10px;
}

form>div>div {
  margin: auto;
}

form label {
  display: block;
  margin: 5px 0 0 0;
}

form button {
  display: block;
  margin: 20px 0 0 0;
  font-weight: 600;
}

form input,
form select {
  min-width: 200px;
  min-height: 25px;
}

.form-grid {
  display: grid;
  grid-template-columns: 1fr;
}

@media screen and (min-width: 550px) {
  .form-grid {
    grid-template-columns: 1fr 1fr;
  }
}
